.footer{
  background-color: #d9d9d9;
  padding-top: 20px;
  padding: 20px 0;
  color: #8d8d8e;
  border-top: 1px #d1d1d1 solid;
  @include big-tablet {
    .footer {
      font-size: 14px;
    }
  }
  @include tablet {
    .footer {
      width: 100%;
      max-width: 968px;
    }
    .footercontainer{
      height: 300px;
    }
    .footer-info {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      justify-content: center;
    }
    .footer-text {
      display: flex;
      justify-content: center;
      width: 100%;
      text-align: center;
      align-items: center;
      margin-bottom: 20px;
      .company {
        padding: 0;
        margin: 0;
      }
    }
    .footer-menu {
      flex-direction: column;
      & nav {
        display: flex;
        flex-direction: column;
        & a {
          width: 100%;
          max-width: 968px;
          text-align: center;
          margin-bottom: 20px;
        }
      }
    }
    .footer-button {
      margin: 0;
    }
  }
  @include small-tablet {
    .footer {
      width: 100%;
      max-width: 768px;
    }
  }
}

.footer a {
  color: #8d8d8e;
}

.footer-info {
  display: flex;
  justify-content: space-between;
}

.footer-text{
  display: flex;
  align-items: center;

}

.copyright{
  padding-right: 5px;
  font-size: 14px;
}
.company{
  padding-right: 16px;
  margin-right: 16px;
  font-size: 14px;
}
.footer-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  .simplebar-placeholder {
    display: none;
  }
}
.footer-menu a {
  text-decoration: none;
  font-size: 14px;
  display: inline-block;
  margin-right: 30px;
}
.footer-menu a:hover{
  color: #569921;
}
.footer-button {
  background-color: transparent;
  border-radius: 50px;
  border: 1px solid #8d8d8e;
  padding: 8px 22px
}
.footercontainer{
  width: 100%;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}
