#box4 {
  position: relative;
  width: 100%;
  height: 600px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@include tablet {

  #box4 {
    width: 100%;
    max-width: 968px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 950px;
  }

  #box4 .box4-container {
    width: 85%;
    height: 800px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
  }
  #box4 .box4-services {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;
  }
  #box4 .box4-header {
    width: 100%;
    text-align: center;
  }
}

@include small-tablet {

  #box4 {
    width: 100%;
    max-width: 768px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 950px;
  }

  #box4 .box4-container {
    width: 85%;
    height: 800px;
    margin: auto;
    padding-top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
  }
  #box4 .box4-services {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;
  }
  #box4 .box4-header {
    width: 100%;
    text-align: center;
  }
}

@include mobile {
  #box4 {
    height: 1600px;
    padding: 0;
    margin: 0;
  }
  #box4 .container {
    height: 100%;
  }
}

@include small-mobile {
  #box4 {
    width: 100%;
    max-width: 320px;
  }
  #box4 .container {
    height: 100%;
  }
}

.box4-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba($color: #000000, $alpha: 0.5);
}
.box4-header {
  position: absolute;
  width: 100%;
  margin-top: 20px;
  max-width: 1050px;
  display: flex;
  align-items: center;
  z-index: 100;
  justify-content: center;
  font-size: 32px;
  color: #fff;
  @include mobile {
    font-weight: 600;
    font-size: 24px;
    line-height: 23px;
    width: 90%;
    max-width: 500px;
  }
}
.box4-container {
  width: 100%;
  height: 100%;
  padding-top: 150px;
  display: flex;
  justify-content: center;
}
.box4-container .box4-services {
  position: relative;
  display: flex;
  justify-content: center;
  width: 255px;
  height: 100%;
  max-height: 350px;
  margin-top: 25px;
  background-color: #000;
  border-radius: 25px;
}
.box4-container .box4-services:not(:last-child) {
  margin-right: 25px;
}
.box4-container .box4-services .box4-services-bg {
  position: absolute;
  border-radius: 25px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.6;
}
.box4-container .box4-services .box4-services-header {
  position: absolute;
  display: flex;
  top: 25%;
  color: #fff;
  font-size: 24px;
  text-align: center;
  justify-content: center;
}
.box4-container .box4-services .box4-services-btn {
  position: absolute;
  display: flex;
  top: 65%;
  color: #fff;
  justify-content: center;
  border: 1px #fff solid;
  margin-top: 30px;
  padding: 10px 20px;
  border-radius: 25px;
}

.box4-container .box4-services .box4-services-btn .box4-services-btn-header {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}
.box4-container .box4-services .box4-services-btn .box4-services-btn-header:hover {
  color: #d9d9d9;
  transition: all .3s ease-in-out;
}

