.infomenu {
  width: 100%;
  height: 100px;
  align-items: center;
  &__container {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & img {
      margin-right: 30px;
    }
  }
  &_namecompany{
    margin: 0;
    font-size: 16px;
    &_name{
      margin: 0;
      padding: 0;
      font-size: 24px;
      font-weight: 600;
      line-height: 38px;
    }
    &_descr {
      margin: 0;
      padding: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
    }
    @include mobile {
      display: none;
    }
  }
  &_scrolled {
    position: fixed;
    top: 0;
    z-index: 600;
    background-color: #d9d9d9;
    animation-name: fadeInUp;
    animation-duration: 1s;
    height: 70px;
    display: inline-flex;
    transition: all .3s ease-in-out;
    .infomenu__container {
      height: 50px;
      margin: 0 auto;
      padding: 0 var(--container-offset);
      width: 100%;
      max-width: var(--container-width);
    }
    .infomenu_logo {
      height: 50px;
    }
    .infomenu_logo img {
      height: 50px;
      width: 50px;
    }
  }
}
