#box5 {
  width: 100%;
  height: 760px;
  display: flex;
  background-color: #d9d9d9;
}
.box5-header {
  text-align: center;
  font-size: 32px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.box5-container {
  display: flex;
  justify-content: center;
}
.box5-map-600 {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  max-width: 600px;
  height: 100%;
  max-height: 600px;
  margin-top: 60px;
  margin-right: 150px;
}
.box5-map-300 {
  display: none;
}
.box5-info {
  width: 100%;
  height: 100%;
  max-width: 900px;
  max-height: 600px;
}
@include desktop1100 {
  #box5 {
    width: 100%;
    max-width: 1100px;
    height: 1300px;
  }

  #box5 .container .box5-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  #box5 .container .box5-container .box5-map-600 {
    width: 100%;
    margin: auto;
  }

  #box5 .container .box5-container .box5-info {
    width: 96%;
    height: 1000px;
    margin-top: 25px;
  }
  #box5 .container .box5-container .box5-info p {
    width: 96%;
    height: 400px;
  }
}
@include tablet {
  #box5 {
    width: 100%;
    max-width: 968px;
  }
}
@include small-tablet {
  #box5 {
    width: 100%;
    max-width: 768px;
  }
}
@include mobile {
  .box5-map-300 {
    display: block;
  }
  .box5-map-600 {
    display: none;
  }
  .box5-info {
    width: 100%;
    height: 100%;
    max-width: 576px;
    padding: 0 15px;
  }
}
